const tipus = (fegyver?: Fegyver): fegyverTipus | undefined => {
    if (!fegyver || fegyver.name === FEGYVERTELEN.name) {
        return undefined;
    }
    if (fegyver.harcertek.ce > 0) {
        return 'lofegyver';
    }
    if (fegyver.name.toLowerCase() === 'kl') {
        return 'kl';
    }
    if (fegyver.pajzs) {
        return 'pajzs';
    }
    return 'kozelharc';
}

const kepzettseg = (fegyver: Fegyver, dobas?: boolean): string => {
    if (fegyver.name.toLowerCase() === 'kl') {
        return 'klharc';
    }
    if (dobas) {
        return 'Fegyverdobs - ' + fegyver.name.toLowerCase();
    } else {
        return 'Fegyverhasznlat - ' + fegyver.name.toLowerCase();
    }
}

export const FegyverUtils = {
    tipus,
    kepzettseg
}

